	module.exports = {"data":{"markdownRemark":{"html":"<h3><a href=\"https://css-tricks.com/javascript-learning-landscape-2018/\">The JavaScript Learning Landscape in 2018</a></h3>\n<p>介绍了如何来学习JS，从读听看做四个纬度来介绍，以及作者自己制定的学习计划。</p>\n<h3><a href=\"https://medium.com/@kelin2025/so-you-wanna-use-es6-modules-714f48b3a953\">How to write and build JS libraries in 2018</a></h3>\n<p>介绍了如何来写一个ji库，一些作者的意见和建议。\n主要围绕三个问题：</p>\n<ul>\n<li>Why using bundlers and transpilers in libraries may turn into a problem?</li>\n<li>How to build libraries correctly?</li>\n<li>Why I said not to use imports/exports? Do you really need it?</li>\n</ul>\n<h3><a href=\"https://medium.com/webpack/webpack-4-released-today-6cdb994702d4\">webpack 4: released today!!Codename: Legato</a></h3>\n<p><strong>🕵️‍新特性</strong></p>\n<ul>\n<li>🏎 webpack 4, is FAST (up to 98% faster)!</li>\n<li>😍 Mode, #0CJS, and sensible defaults</li>\n<li>✂ Goodbye CommonsChunkPlugin</li>\n<li>WebAssembly Support</li>\n<li>🐐 Module Type’s Introduced + .mjs support</li>\n</ul>\n<h3><a href=\"https://www.dartlang.org/\">Dart2即将到来</a></h3>\n<p>一个可以写web端，手机端应用的语言。\n<a href=\"https://www.dartlang.org/dart-2\">升级指南</a></p>\n<h3><a href=\"https://laozhu.me/post/html-head-best-practices/\">HTML 文档之 Head 最佳实践</a></h3>\n<p>主要介绍了Head元素内各个元素属性的用法，包括viewport,dns欲读取，css欲加载欲请求，文档信息，社会化分享等内容</p>","frontmatter":{"date":"February 27, 2018","path":"/blog/weekly/2","title":"前端每周进阶 第2期"}}},"pathContext":{}}

